$accent-color: #59acb5;
$primary-color: #e0fcff;
$secondary-color: #3d8fa8;
$tertiary-color: #1f2833;
$gray-color: #c5c6c7;
$light-gray: #e9e9e9;
$react-native-color: #53b8d4;
$graphql-color: #de33a6;

#project-details {
  background: $light-gray;

  .project-details {
    margin: 0 auto;
    width: 80%;
    max-width: 1200px;
    padding: 5em 0;
    display: flex;
    flex-direction: column;
  }

  h1 {
    font-size: 2.5rem;
    margin-bottom: 1em;
    color: $secondary-color;
  }

  h2 {
    text-align: center;
    font-size: 2rem;
    color: $secondary-color;
    margin-top: 5em;
    margin-bottom: 3em;
  }

  p {
    letter-spacing: 0.025em;
    max-width: 800px;
    line-height: 1.8em;
    margin-bottom: 1.5em;
  }

  .section__title {
    font-size: 1.25em;
    font-weight: 600;
  }

  .yohan-icon {
    width: 60px;
    box-shadow: none;
    margin-top: 1rem;
    margin-left: 2rem;
  }

  picture {
    margin: 0 auto;
    img {
      //   width: 800px;
      margin: 0 auto;
      width: min-content;
      margin-bottom: 5rem;
      border-radius: 0.75em;
      box-shadow: 8px 8px 16px 0px rgba($tertiary-color, 0.4);
    }
  }

  .details-section {
    display: flex;
    gap: 2rem;
    margin-bottom: 20%;
  }

  a {
    width: fit-content;
    font-size: 1.25rem;
    font-weight: 500;
    color: black;
    text-decoration-thickness: 2px;
    margin-bottom: 2em;

    &:not(:last-of-type) {
      font-size: 1rem;
    }
  }

  span {
    font-weight: 600;
    color: $graphql-color;

    &:first-of-type {
      color: $react-native-color;
    }
  }
}

@media screen and (max-width: 1480px) {
  #project-details {
    .project-details {
      width: 850px;
    }
  }
}

@media screen and (max-width: 475px) {
  #project-details {
    .project-details {
      width: 90%;
    }

    .details-section {
      flex-direction: column;
    }

    picture {
      img {
        border-radius: 0.35em;
      }
    }

    .yohan-icon {
      width: 40px;
    }
  }
}
