#project-details {
  background: #e9e9e9;
}
#project-details .project-details {
  margin: 0 auto;
  width: 80%;
  max-width: 1200px;
  padding: 5em 0;
  display: flex;
  flex-direction: column;
}
#project-details h1 {
  font-size: 2.5rem;
  margin-bottom: 1em;
  color: #3d8fa8;
}
#project-details h2 {
  text-align: center;
  font-size: 2rem;
  color: #3d8fa8;
  margin-top: 5em;
  margin-bottom: 3em;
}
#project-details p {
  letter-spacing: 0.025em;
  max-width: 800px;
  line-height: 1.8em;
  margin-bottom: 1.5em;
}
#project-details .section__title {
  font-size: 1.25em;
  font-weight: 600;
}
#project-details .yohan-icon {
  width: 60px;
  box-shadow: none;
  margin-top: 1rem;
  margin-left: 2rem;
}
#project-details picture {
  margin: 0 auto;
}
#project-details picture img {
  margin: 0 auto;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  margin-bottom: 5rem;
  border-radius: 0.75em;
  box-shadow: 8px 8px 16px 0px rgba(31, 40, 51, 0.4);
}
#project-details .details-section {
  display: flex;
  gap: 2rem;
  margin-bottom: 20%;
}
#project-details a {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 1.25rem;
  font-weight: 500;
  color: black;
  text-decoration-thickness: 2px;
  margin-bottom: 2em;
}
#project-details a:not(:last-of-type) {
  font-size: 1rem;
}
#project-details span {
  font-weight: 600;
  color: #de33a6;
}
#project-details span:first-of-type {
  color: #53b8d4;
}

@media screen and (max-width: 1480px) {
  #project-details .project-details {
    width: 850px;
  }
}
@media screen and (max-width: 475px) {
  #project-details .project-details {
    width: 90%;
  }
  #project-details .details-section {
    flex-direction: column;
  }
  #project-details picture img {
    border-radius: 0.35em;
  }
  #project-details .yohan-icon {
    width: 40px;
  }
}/*# sourceMappingURL=projectDetails.css.map */